import React from 'react';
import './Card.css';

const ProjectCard = () => {
    return (
        <div class="card">
            <div class="additional">
                <div class="more-info">
                    <h2 style={{padding:'2rem 1rem 1rem 1rem'}}>Simple Weather App</h2>
                    <div class="coords">
                    <span>Created: </span>
                    <span>August 2019</span>
                    </div>
                    <div class="coords">
                    <span></span>
                    <span></span>
                    </div>
                    <div class="stats">
                    <div>
                        <div class="title">React.JS</div>
                        <i class="fab fa-react"></i>
                    </div>
                    <div>
                        <div class="title">HTML5</div>
                        <i class="fab fa-html5"></i>
                    </div>
                    <div>
                        <div class="title">CSS3</div>
                        <i class="fab fa-css3"></i>
                    </div>
                    <div>
                        <div class="title">Sass</div>
                        <i class="fab fa-sass"></i>
                    </div>
                    </div>
                </div>
            </div>
            <div class="general">
                <h2>Simple Weather App</h2>
                <p>Weather application using OpenWeatherMapAPI and created in React.JS</p>
                <span class="more">Mouse over the card for more info</span>
            </div>
        </div>
    );
};

export default ProjectCard;